import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Neighborhood } from "../neighborhood/neighborhood";
import { RestProvider } from "../rest/rest";

@Injectable()
export class CityProvider {

  constructor(public http: HttpClient, public restProvider: RestProvider) {
  }

  public getAllCities() {
    return new Promise(resolve => {
      this.http.get(this.restProvider.API_URL + "get-all-cities.php", { headers: this.restProvider.headers })
        .subscribe(data => {
          resolve(data);
        }, error => {
          console.error(error);
          this.restProvider.sendGoogleAnalyticsErrorData(
            'CityProvider',
            'getAllCities',
            error);
          resolve(error);
        });
    });
  }

  // @annotateName
  public getAllCitiesWithPlan() {
    return new Promise(resolve => {
      this.http.get(this.restProvider.API_URL + "get-all-cities-with-plan.php", { headers: this.restProvider.headers })
        .subscribe(data => {
          resolve(data);
        }, error => {
          console.error(error);
          this.restProvider.sendGoogleAnalyticsErrorData(
            'CityProvider',
            'getAllCitiesWithPlan',
            error);
          resolve(error);
        });
    });
  }
}

export class City {
  id: number;
  name: string;
  neighborhoods: Neighborhood[];
}



